import React from 'react'
import { SeactionHeader } from '../../../../components';
import { renderProdcuts } from './data';


export default function CharacteristicProducts() {
    return (
        <section className='m-section'>

            <div className='container'>

                <SeactionHeader href={''} title={'منتجات مميزة'} paragraph={'تسوق احدث المنتجات المميزة المضافة جديد'} ></SeactionHeader>

                <div className='grid grid-cols-12 gap-5 md:gap-10 mt-20'>

                    {renderProdcuts}

                </div>

            </div>

        </section>
    )
}
