import { ProductCard } from "../../../../components";

export const renderProdcuts = ['product-one', 'product-two', 'product-one', 'product-two', 'product-three', 'product-four', 'product-five', 'product-six'].map((product, index) => {
    return <ProductCard key={index} hasDiscount={index % 2 === 0} hasOffer={index % 2 !== 0} classNames={'col-span-12 sm:col-span-6 lg:col-span-4 xl:col-span-3'} imageName={product}></ProductCard>
});
