import React from 'react'
import { SeactionHeader } from '../../../../components'
import { renderArticles } from './data'

export default function LatestArticles() {
    return (

        <section className='m-section !py-16 bg-[var(--off-white)]'>

            <div className='container'>

                <SeactionHeader href={''} title={'أحدث المقالات'} paragraph={'تسوق احدث المنتجات المميزة المضافة جديد'}></SeactionHeader>

                <div className='grid grid-cols-12 gap-5 my-20'>

                    {renderArticles}

                </div>

            </div>

        </section>
    )
}

