import { ArticleCard } from "../../../../components";

const articles = [
    { imageSrc: "article-one", date: 10 },
    { imageSrc: "article-two", date: 12 },
    { imageSrc: "article-three", date: 15 },
];

export const renderArticles = articles.map(article => {
    return (
        <div key={article.date} className='col-span-12 lg:col-span-4 mb-5 lg:mb-0'>

            <ArticleCard imageSrc={article.imageSrc} date={article.date}></ArticleCard>

        </div>
    )
})