import bg from '../../assets/images/hero-home-bg.png';
import { SwiperWrapper } from '../../../../components';
import { items } from './data';

export default function Hero() {

    return (
        <section className={`p-2 relative custom-pagination-wrapper hero-navigation`}>

            <img src={bg} className='absolute left-0 top-0 z-[-1] w-full h-full max-w-full max-h-full' style={{ objectFit: 'fill' }} />

            <div className='container overflow-hidden relative min-h-[600px] flex items-center justify-center flex-col'>

                <div className='w-full py-10 relative'>

                    <SwiperWrapper
                        items={items}
                        slidesPerViewCount={[1, 1, 1, 1]}
                        includeNavigation={true}
                        autoplayDelay={8000}
                        isLooped={true}
                        includePagination={true}
                    ></SwiperWrapper>

                </div>


            </div>

        </section>
    )
}
