import { SwiperSlide } from "swiper/react";
import { ProductCard } from "../../../../components";

export const items = ['hedphone', 'airpods', 'sub', 'smart-watch', 'hedphone', 'airpods', 'sub', 'smart-watch', 'hedphone', 'airpods', 'sub', 'smart-watch', 'hedphone', 'airpods', 'sub', 'smart-watch'].map((product, index) => {
    return (
        <SwiperSlide key={index}>
            <ProductCard hasDiscount={index % 2 === 0} hasOffer={index % 2 !== 0} classNames={'col-span-6 md:col-span-4 xl:col-span-3'} imageName={product}></ProductCard>
        </SwiperSlide>
    )
});