import bg from "../../assets/images/best-offer-bg.png";
import { SwiperWrapper } from "../../../../components";
import { items } from "./data";

export default function BestOffers() {
    return (

        <section className='m-section !py-16 min-h-[500px] relative flex flex-col items-center custom-pagination-wrapper'>

            <img src={bg} className='absolute left-0 top-0 z-[-1] w-full h-full max-w-full max-h-full' style={{ objectFit: 'fill' }} />

            <div className='container overflow-hidden'>

                <SwiperWrapper
                    items={items}
                    slidesPerViewCount={[1, 1, 1, 1]}
                    includeNavigation={false}
                    autoplayDelay={8000}
                    isLooped={true}
                    includePagination={true}
                ></SwiperWrapper>

                <div className="flex items-center justify-center">
                    <button className="min-btn">اكتشف المزيد</button>
                </div>

            </div>


        </section>
    )
}
