import React from 'react'
import { Rating, Timer } from '../../../../../../components'
import { faHeart } from '@fortawesome/free-regular-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCartPlus } from '@fortawesome/free-solid-svg-icons'

export default function ProductCard() {
    return (
        <div className='col-span-12 lg:col-span-6 mb-5 lg:mb-0 p-1 sm:p-5 flex items-center flex-col lg:flex-row rounded-[10px] border-2 border-[var(--min-color)]'>

            <div className='relative w-full lg:w-[20%] h-[200px]'>

                <img src={require('../../../../assets/images/watch.png')} className='w-full h-full hidden lg:block' style={{ objectFit: 'fill' }} />

                <img src={require('../../../../assets/images/watch.png')} className='absolute left-0 top-0 w-full h-full max-w-full max-h-full' style={{ objectFit: 'cover' }} />


            </div>

            <div className='ms-5 flex-1'>

                <p className='text-[var(--min-color)] font-medium mb-3'>ساعات</p>

                <h2 className='font-medium mb-3 text-[18px]'>ساعة ذكية جديدة من سلسلة 8</h2>
                <p className='mb-3 gray-text'>سوار رياضي اسود - عادي.</p>

                <div className='my-2 flex items-center'>

                    <Rating />

                    <p className='text-[#A5A5A5] !text-[11px] ms-3'>(4.5)</p>

                </div>

                <div className='my-2 flex items-center'>

                    <h6 className='text-[var(--danger-color)] font-bold ms-3'>250.00 رس</h6>

                    <p className='gray-text !text-[#A5A5A5] ms-3' style={{ textDecoration: 'line-through' }}>350.00 ر.س</p>

                </div>

                <div className='my-2 w-fit'>

                    <Timer />

                </div>


                <div className='flex items-center justify-between mb-2 mt-5'>

                    <button className='flex items-center justify-center bg-[var(--min-color)] text-white font-medium rounded-[5px] border p-3 flex-1'>

                        <FontAwesomeIcon icon={faCartPlus} />

                        <span className='ms-3'>أضف للسلة</span>
                    </button>

                    <button className='flex items-center justify-center rounded-[5px] border ms-3 p-4 text-[var(--gray-color)]'>
                        <FontAwesomeIcon icon={faHeart}></FontAwesomeIcon>
                    </button>


                </div>

            </div>

        </div>
    )
}
