import { Link } from "react-router-dom";

const data = [
    { name: "معالجة أسرع وأكثر ثراءً", bgColor: "#E0F6F0", describe: "أسرع من EXPEED 4 ، يعالج EXPEED 5 بسرعة 45.7 ميجابكسل من البيانات لتقليل التشويش", imageSrc: "smart-watch" },
    { name: "معالجة أسرع وأكثر ثراءً", bgColor: "#FFEFDA", describe: "أسرع من EXPEED 4 ، يعالج EXPEED 5 بسرعة 45.7 ميجابكسل من البيانات لتقليل التشويش", imageSrc: "phone" },
    { name: "معالجة أسرع وأكثر ثراءً", bgColor: "#FDDCDD", describe: "أسرع من EXPEED 4 ، يعالج EXPEED 5 بسرعة 45.7 ميجابكسل من البيانات لتقليل التشويش", imageSrc: "airpods" },
];

export const renderProduct = data.map((product, index) => {
    return (
        <div key={index} className={`col-span-12 lg:col-span-4 rounded-[3px] p-8 py-10 flex items-center justify-center flex-col`} style={{ backgroundColor: product.bgColor }}>

            <img src={require(`../../assets/images/${product.imageSrc}.png`)} />

            <h2 className='mt-6 mb-3 text-[18px] font-medium'>{product.name}</h2>

            <p className='gray-text text-center sm:max-w-[70%] mb-3'>{product.describe}</p>

            <Link to={'products'}>
                <button className='animt-btn'>تسوق الآن</button>
            </Link>

        </div>
    )
})