import React from 'react'
import { SeactionHeader } from '../../../../components'
import { ProductCard } from './component'

export default function OffersNearToEnd() {
    return (
        <section className='m-section'>

            <div className='container'>

                <h1 className='font-bold text-[18px] sm:text-[25px] mb-2'>عروض تنتهى قريبا</h1>

                <SeactionHeader href={''} title={'منتجات مميزة'} paragraph={'تسوق احدث المنتجات المميزة المضافة جديد'} ></SeactionHeader>

                <div className='grid grid-cols-12 my-10 gap-3 lg:gap-10'>
                    <ProductCard />
                    <ProductCard />
                </div>

            </div>

        </section>
    )
}
