import React from 'react'
import bg from '../../assets/images/var-banner.jpg';

export default function BannerTwo() {
    return (
        <div className='m-section !py-24 relative' style={{backgroundImage: `url(${bg})` , backgroundPosition: "100% 25%" , backgroundSize: 'cover'}}>

            <div className='container'>

                <div className='lg:max-w-[30%] text-center'>

                    <h1 className={`text-white font-bold text-[18px] sm:text-[35px] lg:text-[45px] transition mb-5`}>رؤيه واضحه للعالم</h1>

                    <p className={`mb-5 text-white text-[12px] sm:text-[14px]`}>متجر سله يوفر لك كل ماتحتاجه من الكترونيات او اثاث منزلي بالاضافه الي افضل التخفيضات علي المتجاتو تسوق الان واستمتع بكل التخفيضات علي المنتجات</p>

                </div>

            </div>

        </div>
    )
}
