import React from 'react'
import { SwiperWrapper } from '../../../../components';
import { items } from './data';

export default function CommercialBrands() {
    return (
        <section className='m-section !py-16'>

            <div className='container'>

                <div className='mt-10 relative top-left-pagination'>

                    <div className='mb-20'>
                        <h2 className='font-bold text-[18px] sm:text-[22px] mb-2'>آراء العملاء</h2>
                        <p className='gray-text lg:ms-16'>بيتم استعراض بعض آراء وتعليقات المستخدمين</p>
                    </div>

                    <SwiperWrapper
                        items={items}
                        includeNavigation={true}
                        slidesPerViewCount={[2.5, 3, 5, 5]}
                        autoplayDelay={5000}
                        isLooped={true}
                    ></SwiperWrapper>

                </div>

            </div>

        </section>
    )
}
