import React from 'react'
import { SwiperWrapper } from '../../../../components';
import { addsData, items } from './data';


export default function Categories() {

    return (

        <section className='m-section'>

            <div className='container relative top-left-pagination'>

                <h1 className='font-bold text-[18px] sm:text-[25px] mb-2'>تسوق حسب الفئات</h1>

                <div className='mb-20'>
                    <h2 className='font-bold text-[16px] sm:text-[22px] mb-2'>منتجات مميزة</h2>
                    <p className='gray-text lg:ms-16'>تسوق احدث المنتجات المميزة المضافة جديد</p>
                </div>

                <div className='text-center my-8'>


                    <SwiperWrapper
                        items={items}
                        includeNavigation={true}
                        slidesPerViewCount={[2.5, 4.5, 6.5, 8.5]}
                        autoplayDelay={5000}
                    ></SwiperWrapper>

                </div>

                <div className='grid grid-cols-12 gap-5 my-20'>

                    {
                        addsData.map((addv, index) => {
                            return (
                                <div key={index} className={`col-span-12 lg:col-span-4 relative flex flex-col justify-around min-h-[229px] p-5`}>

                                    <img src={`${require(`./assest/images/adv-${addv.imageSrc}.png`)}`} className='absolute left-0 top-0 z-[-1] w-full max-w-full max-h-full' style={{ objectFit: 'fill' }} />

                                    <div className={`${addv.titleTwo ? "flex flex-col items-end" : ""}`}>
                                        <h3 className='text-white'>{addv?.title}</h3>
                                        <h3 className='text-white'>{addv?.titleTwo}</h3>
                                    </div>

                                    <p className='text-white font-medium'>تسوق الآن</p>

                                </div>
                            )
                        })
                    }

                    <div className='col-span-12 p-5 mt-3 relative py-20'>

                        <img src={`${require(`./assest/images/adv-four.png`)}`} className='absolute left-0 top-0 z-[-1] w-full max-w-full max-h-full h-full' style={{ objectFit: 'fill' }} />

                        <h2 className='mb-10 text-center text-white'>سماعات ديجتال</h2>
                        <h2 className='font-medium text-center ms-96 ms text-white'>تسوق الآن</h2>


                    </div>



                </div>

            </div>

        </section>
    )
}
