import { ProductCard } from "../../../../components";

const data = ['special-services-one', 'special-services-two', 'special-services-three', 'special-services-four', 'special-services-five', 'special-services-six', 'special-services-seven', 'special-services-eight'];

export const renderServices = data.map((product, index) => {
    return <ProductCard hasDiscount={index % 2 === 0} hasOffer={index % 2 !== 0} key={index} classNames={'col-span-12 sm:col-span-6 lg:col-span-4 xl:col-span-3'} imageName={product}></ProductCard>
})
