import React from 'react'
import bg from '../../assets/images/home-banner.png';
import { Link } from 'react-router-dom';


export default function Banner() {
    return (

        <div className='m-section !py-20 relative'>

            <img src={bg} className='absolute left-0 top-0 z-[-1] w-full h-full max-w-full max-h-full' style={{ objectFit: 'fill' }} />

            <div className='container'>

                <h1 className={`text-white font-bold text-[18px] sm:text-[25px] md:text-[45px] transition mb-5`}>أفضل التخفيضات 2022</h1>

                <p className={`mb-5 text-white text-[14px]`}>متجر سلة يوفر لك كل ماتحتاجه من إلكترونيات أو أثاث منزلي بالإضافة إلى أفضل التحفيضات على المنتجات , تسوق الان واستمتع بكل بالتخفيضات على المنتجات</p>

                <Link to={'products'}>
                    <button className='min-btn mt-10'>اكتشف المزيد</button>
                </Link>

            </div>

        </div>
    )
}
