import { SwiperWrapper } from '../../../../components';
import { items } from './data';

export default function NewsOffers() {

    return (

        <section className='m-section !py-16 bg-[var(--off-white)]'>

            <div className='container'>


                <div className='mt-10 relative top-left-pagination'>

                    <div className='mb-20'>
                        <h2 className='font-bold text-[18px] sm:text-[22px] mb-2'>وصل حديثا</h2>
                        <p className='gray-text lg:ms-16'>تسوق احدث المنتجات المميزة المضافة جديد</p>
                    </div>

                    <SwiperWrapper
                        items={items}
                        includeNavigation={true}
                        slidesPerViewCount={[2, 2, 3, 4]}
                        autoplayDelay={3000}
                        isLooped={true}
                    ></SwiperWrapper>

                </div>

            </div>

        </section>
    )
}
