import React from 'react'
import {
    carIcon,
    caricleIcon,
    visaIcon,
    boxOneBg,
    boxTwoBg
} from './data'


export default function Features() {
    return (
        <section className='m-section'>

            <div className='container'>

                <div className='grid grid-cols-12 gap-7 lg:border lg:border-[var(--gray-color)] rounded-[5px] lg:p-7'>

                    <div className='col-span-12 lg:col-span-4 border justify-center lg:justify-start lg:mb-0 flex items-center p-4'>

                        <img src={carIcon} className='me-5' />

                        <div>

                            <h2 className='font-medium mb-1 text-[20px]'>منتجات مضمونه</h2>

                            <h5 className='text-[var(--text-color)] font-medium'>مدفوعات آمنة أقساط تصل إلى 12 شهرًا</h5>

                        </div>

                    </div>
                    <div className='col-span-12 lg:col-span-4 border justify-center lg:justify-start lg:mb-0 flex items-center p-4 border-l border-r'>

                        <img src={caricleIcon} className='me-5' />

                        <div>

                            <h2 className='font-medium mb-1 text-[20px]'>منتجات مضمونه</h2>

                            <h5 className='text-[var(--text-color)] font-medium'>مدفوعات آمنة أقساط تصل إلى 12 شهرًا</h5>

                        </div>

                    </div>
                    <div className='col-span-12 lg:col-span-4 border justify-center lg:justify-start lg:mb-0 flex items-center p-4'>

                        <img src={visaIcon} className='me-5' />

                        <div>

                            <h2 className='font-medium mb-1 text-[20px]'>منتجات مضمونه</h2>

                            <h5 className='text-[var(--text-color)] font-medium'>مدفوعات آمنة أقساط تصل إلى 12 شهرًا</h5>

                        </div>

                    </div>

                </div>

                <div className='grid grid-cols-12 gap-7 mt-32'>

                    <div className='col-span-12 lg:col-span-6 p-12 flex flex-col items-end' style={{ backgroundImage: `url(${boxTwoBg})`, backgroundSize: 'cover' }}>

                        <p className='gray-text'>سماعات ديجتال</p>
                        <h1 className='text-[30px] my-3 font-bold'>أفضل صوت نقي</h1>
                        <p className='font-medium'>تسوق الآن</p>

                    </div>
                    <div className='col-span-12 lg:col-span-6 p-12 flex flex-col items-end' style={{ backgroundImage: `url(${boxOneBg})`, backgroundSize: 'cover' }}>

                        <p className='gray-text'>سماعات ديجتال</p>
                        <h1 className='text-[30px] my-3 font-bold'>أفضل صوت نقي</h1>
                        <p className='font-medium'>تسوق الآن</p>

                    </div>

                </div>

            </div>

        </section>
    )
}
